                        // 
                        ctx.beginPath();
                        ctx.arc(0, -size * 0.2, size * 0.5, 0, Math.PI * 2);
                        ctx.fill();
                        
                        ctx.beginPath();
                        ctx.moveTo(-size * 0.5, -size * 0.2);
                        ctx.quadraticCurveTo(
                            0, size * 0.8,
                            size * 0.5, -size * 0.2
                        );
                        ctx.fill();
                        
                        // 
