                        ctx.lineTo(labelWidth/2 - cornerRadius, -labelHeight/2);
                        ctx.quadraticCurveTo(labelWidth/2, -labelHeight/2, labelWidth/2, -labelHeight/2 + cornerRadius);
                        ctx.lineTo(labelWidth/2, labelHeight/2 - cornerRadius);
